/** ========================================
 * INIT
 * ====================================== */
*,
* ::before,
* ::after {
  box-sizing: border-box; }

body {
  min-height: 100vh;
  background: #daecef;
  background: linear-gradient(to bottom, #daecef 50%, #b6dae0 100%);
  color: #464c4c;
  font-size: 100%;
  line-height: 1; }

img {
  width: 100%; }

/** ========================================
 * HEADER
 * ====================================== */
header {
  overflow: hidden;
  background: #daecef;
  box-shadow: 2px 2px 3px #aac6cc; }

.title {
  padding: .5rem;
  color: #daecef;
  font-size: 3.1rem;
  font-weight: 700;
  text-align: center;
  text-shadow: 4px 4px 6px #aac6cc, -2px -2px 3px rgba(255, 255, 255, 0.75), -1px -1px 2px rgba(255, 255, 255, 0.45); }
  .title a {
    color: #daecef;
    text-decoration: none; }

/** ========================================
 * FOOTER
 * ====================================== */
footer {
  margin-top: 1.2rem;
  padding: 1rem;
  text-align: center; }

/** ========================================
 * MAIN
 * ====================================== */
main {
  width: 100vw;
  margin: 0 auto;
  padding: 0 .5rem; }

header + main {
  margin-top: 2rem; }

/** ========================================
 * SINGLE
 * ====================================== */
.contents {
  overflow: hidden;
  background-color: #ecf5f7;
  border-radius: 8px;
  box-shadow: 2px 2px 3px #aac6cc; }
  .contents:not(:first-child) {
    margin-top: 3.6rem; }
  .contents_img {
    margin-top: -100px;
    transform: translate3d(0, 100px, -1px); }
  .contents_wrap {
    padding: 1rem;
    background-color: #e4f2f4;
    border-radius: 8px;
    box-shadow: inset 0 1px 1px #fff, 0 1px 2px #aac6cc;
    transform: translateZ(0); }
  .contents_date {
    border-bottom: 3px solid #a1cfd6;
    font-size: 0.875rem; }
  .contents_title {
    margin-top: 1.6rem;
    font-size: 1.7rem;
    word-break: break-all; }
    .contents_title a {
      text-decoration: none;
      color: currentColor; }
      .contents_title a:hover {
        text-decoration: underline; }
  .contents_body {
    margin-top: 1.2rem;
    line-height: 1.6; }
    .contents_body p:not(:first-child) {
      margin-top: .8rem; }
  .contents_cate {
    margin-top: 1rem;
    text-align: right;
    font-size: 0.875rem; }
    .contents_cate ul {
      display: flex;
      justify-content: flex-end; }
      .contents_cate ul li {
        margin-left: .5rem; }
  .contents_tag {
    margin-top: 1rem;
    text-align: right;
    font-size: 0.875rem; }

.tag_icon {
  position: relative;
  display: inline-block;
  width: .5rem;
  height: .5rem;
  margin-right: .5rem;
  background: #85c0ca; }
  .tag_icon::before {
    content: "";
    display: inline-block;
    width: .8rem;
    height: calc(.5rem * 1.41);
    background: #85c0ca;
    transform: translateX(0.5rem) rotate(45deg);
    transform-origin: left top; }
  .tag_icon::after {
    content: "";
    position: absolute;
    display: inline-block;
    top: 50%;
    left: 50%;
    width: 3px;
    height: 3px;
    background: #daecef;
    border-radius: 50%;
    transform: translate(-50%, -50%); }

/** ========================================
 * ARCHIVE
 * ====================================== */
/**/
.card {
  display: flex;
  align-items: center;
  padding: .5rem;
  background-color: #ecf5f7;
  border-radius: 8px;
  box-shadow: inset 0 1px 1px #fff, 2px 2px 3px #aac6cc; }
  .card:not(:first-child) {
    margin-top: 1.2rem; }
  .card_img {
    overflow: hidden;
    flex: 0 0 16vw;
    height: 16vw;
    border-radius: 6px;
    box-shadow: 2px 2px 3px #aac6cc; }
    .card_img img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
  .card_body {
    flex: 1 1 auto;
    margin-left: 1rem;
    line-height: 1.4; }
  .card_title {
    display: block;
    font-weight: 700; }
  .card_excerpt, .card_date {
    font-size: 0.875rem; }
